import { create } from "zustand";
import { persist, createJSONStorage } from "zustand/middleware";
import { Car } from "@/types/cars";

interface CarsState {
  cars: Car[];
  favorites: string[]; // Зберігаємо масив ID обраних авто
  isLoading: boolean;
  error: string | null;

  // Методи для роботи з обраним
  toggleFavorite: (carId: string) => void;

  // Методи для завантаження даних
  setCars: (cars: Car[]) => void;
  setLoading: (status: boolean) => void;
}

export const useCarsStore = create<CarsState>()(
  persist(
    (set) => ({
      cars: [],
      favorites: [],
      isLoading: false,
      error: null,

      // Логіка додавання/видалення з обраного
      toggleFavorite: (carId) =>
        set((state) => ({
          favorites: state.favorites.includes(carId)
            ? state.favorites.filter((id) => id !== carId) // Видаляємо, якщо вже є
            : [...state.favorites, carId], // Додаємо, якщо немає
        })),

      setCars: (cars) => set({ cars }),
      setLoading: (status) => set({ isLoading: status }),
    }),
    {
      name: "rental-cars-storage", // Назва ключа в LocalStorage
      storage: createJSONStorage(() => localStorage),
      // Зберігаємо ТІЛЬКИ список обраних, щоб не кешувати весь каталог
      partialize: (state) => ({ favorites: state.favorites }),
    },
  ),
);
