import Image from "next/image";
import { useCarsStore } from "@/store/useCarsStore";
import { Car } from "@/types/cars";
import css from "./CarCard.module.css";

interface CarCardProps {
  car: Car;
}

export default function CarCard({ car }: CarCardProps) {
  // Підключаємо логіку обраного зі стору
  const favorites = useCarsStore((state) => state.favorites);
  const toggleFavorite = useCarsStore((state) => state.toggleFavorite);

  // Перевіряємо, чи ця машина в списку обраних
  const isFavorite = favorites.includes(car.id);

  // Форматуємо адресу (Місто | Країна)
  const addressParts = car.address.split(",");
  const city = addressParts[1]?.trim();
  const country = addressParts[2]?.trim();

  // Форматуємо пробіг: 5000 -> 5 000 за вимогою ТЗ
  const formattedMileage = car.mileage.toLocaleString("ru-RU");

  return (
    <li className={css.card}>
      <div className={css.thumb}>
        <Image
          src={car.img}
          alt={`${car.brand} ${car.model}`}
          fill
          className={css.image}
          sizes="300px"
          priority={false}
        />

        {/* Кнопка-сердечко 16х16 */}
        <button
          className={css.heartBtn}
          onClick={() => toggleFavorite(car.id)}
          type="button"
          aria-label="Add to favorite"
        >
          <svg
            width="16"
            height="16"
            className={isFavorite ? css.heartActive : css.heartInactive}
          >
            <use href="#Like"></use>
          </svg>
        </button>
      </div>

      <div className={css.content}>
        <div className={css.header}>
          <p className={css.title}>
            {car.brand} <span className={css.accent}>{car.model}</span>,{" "}
            {car.year}
          </p>
          <span className={css.price}>${car.rentalPrice}</span>
        </div>

        {/* Список тегів з разділювачами */}
        <div className={css.tags}>
          <span>{city}</span>
          <span>{country}</span>
          <span>{car.rentalCompany}</span>
          <span>{car.type}</span>
          <span>{formattedMileage} km</span>
          <span>{car.accessories[0]}</span>
        </div>

        {/* Кнопка переходу на детальну сторінку за ТЗ */}
        <button className={css.btn}>Learn more</button>
      </div>
    </li>
  );
}
