"use client";

import { useCarsStore } from "@/store/useCarsStore";
import CarCard from "@/components/CarCard/CarCard";
import css from "./Catalog.module.css";

export default function CatalogPage() {
  // Використовуємо правильну назву 'cars' замість 'items'
  const cars = useCarsStore((state) => state.cars);
  const isLoading = useCarsStore((state) => state.isLoading);

  return (
    <main className={css.container}>
      {isLoading && <div className={css.loader}>Loading cars...</div>}

      <ul className={css.grid}>
        {/* Додаємо перевірку 'cars?', щоб уникнути помилки, якщо масив порожній */}
        {cars?.map((car) => (
          <CarCard key={car.id} car={car} />
        ))}
      </ul>

      {/* Перевіряємо довжину масиву через правильну змінну */}
      {!isLoading && cars?.length === 0 && (
        <p className={css.empty}>Машини не знайдені. Перевірте API.</p>
      )}
    </main>
  );
}
